export default "import {interval as observableInterval, Observable} from 'rxjs';\n\nimport {map, startWith, take} from 'rxjs/operators';\nimport {Component} from '@angular/core';\n\n@Component({\n  selector: 'app-loader',\n  templateUrl: './progress-indicators.component.html',\n})\nexport class ProgressIndicatorsComponent {\n\n  progress: Observable<number>;\n  progressWithETA: Observable<number>;\n  progressETA: Observable<string>;\n  jobRunning = false;\n  job2Running = false;\n  isLoading = false;\n  isSaving = false;\n\n  longRunningAction() {\n    this.progress = observableInterval(1000).pipe(take(10), map(i => 10 * (i + 1)));\n  }\n\n  longRunningActionWithETA() {\n    const seconds = 30;\n    const observable = observableInterval(1000).pipe(startWith(-1), take(seconds + 1), map(i => i + 1))\n    this.progressWithETA = observable.pipe(map(i => 100 * i / seconds));\n    this.progressETA = observable.pipe(map(i => i < seconds ? `${seconds - i}s verbleiben` : 'Fertig!'));\n  }\n\n  startAsyncJob(job) {\n    if (job === 'job1') {\n      this.jobRunning = true;\n      setTimeout(() => this.jobRunning = false, 3000);\n    } else if (job === 'job2') {\n      this.job2Running = true;\n      setTimeout(() => this.job2Running = false, 3000);\n    }\n  }\n\n  showLoadingOverlay() {\n    this.isLoading = true;\n    setTimeout(() => this.isLoading = false, 4000);\n  }\n\n  showSavingOverlay() {\n    this.isSaving = true;\n    setTimeout(() => this.isSaving = false, 4000);\n  }\n\n}\n";