export default "import {Component, Input, OnInit} from '@angular/core';\nimport {SourceCodeService} from '../../../source-code.service';\n\nconst Prism = require('prismjs');\nrequire('prismjs/components/prism-typescript');\n\n@Component({\n  selector: 'app-code-example',\n  templateUrl: 'code-example.component.html'\n})\nexport class CodeExampleComponent implements OnInit {\n  static STASH_URL = 'https://stash.se.bedag.ch/projects/ENG-LIBS/repos/bedag-demos/browse/bedag-angular-styleguide/src/app/styleguide'\n\n  /**\n   * Base path for the src file names, relative to src/app/styleguide.\n   */\n  @Input()\n  basePath: string;\n  /**\n   * A comma separated list of source file names to display.\n   * html file names may contain an anchor, to only display the content of the code-example with that anchor (rather than the entire file)\n   */\n  @Input()\n  src: string;\n\n  showCode = false;\n  files: { prettyName: string, path: string, language: string, code: string, highlightedCode?: string }[];\n\n  constructor(private sourceCodeService: SourceCodeService) {\n  }\n\n  ngOnInit(): void {\n    this.files = this.src.split(',')\n    .map(s => {\n      const [name, anchor] = s.split('#');\n      const language = name.substring(name.lastIndexOf('.') + 1);\n      const prettyName = name.substring(name.lastIndexOf('/') + 1);\n      const path = this.basePath ? this.basePath.concat('/', name) : name;\n      let code = this.sourceCodeService.sourceCode(path);\n\n      if (anchor) {\n        let i = code.indexOf(s);\n        if (i === -1) {\n          throw new Error('The source file ' + name + ' does not contain the marker ' + s);\n        } else if (code.indexOf(s, i + 1) !== -1) {\n          throw new Error(\n            'The source file ' + name + ' contains the marker ' + s + ' more than once. Please specify a unique marker');\n        }\n        i = code.indexOf('\\n', i) + 3;\n\n        const a = code.indexOf('</app-code-example>', i);\n        code = code.substring(i, a);\n        code = code.split('\\n  ').join('\\n');\n      }\n      return {prettyName, path, language, code};\n    });\n  }\n\n\n  showOrHide() {\n    this.showCode = !this.showCode;\n    if (this.showCode) {\n      for (const file of this.files) {\n        file.highlightedCode = file.highlightedCode || Prism.highlight(file.code, Prism.languages[file.language]);\n      }\n    }\n  }\n\n  getStashUrl(): string {\n    if (this.basePath) {\n      return CodeExampleComponent.STASH_URL.concat('/', this.basePath);\n    } else {\n      const firstFile = this.files[0];\n      if (this.files.length === 1) {\n        return CodeExampleComponent.STASH_URL.concat('/', this.files[0].path);\n      }\n      const dir = firstFile.path.substring(0, firstFile.path.lastIndexOf('/') + 1);\n      return CodeExampleComponent.STASH_URL.concat('/', dir);\n    }\n  }\n}\n";