export default "import {Component, forwardRef, HostBinding, Injectable, Input, ViewChild} from '@angular/core';\nimport {\n  NgbCalendar,\n  NgbDateParserFormatter,\n  NgbDatepickerI18n,\n  NgbDateStruct,\n  NgbInputDatepicker\n} from '@ng-bootstrap/ng-bootstrap';\nimport {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';\nimport {DatepickerI18n} from './datepicker.i18n';\n\n@Injectable()\nexport class SwissDateParserFormatter extends NgbDateParserFormatter {\n\n  parse(value: string): NgbDateStruct | null {\n    if (value) {\n      const date = value.split('.');\n      return {\n        day: parseInt(date[0], 10),\n        month: parseInt(date[1], 10),\n        year: parseInt(date[2], 10)\n      };\n    }\n    return null;\n  }\n\n  format(date: NgbDateStruct | null): string {\n    return date ? `${date.day}.${date.month}.${date.year}` : '';\n  }\n}\n\n@Component({\n  selector: 'app-datepicker',\n  templateUrl: './datepicker.component.html',\n  providers: [\n    {\n      provide: NG_VALUE_ACCESSOR,\n      useExisting: forwardRef(() => DatepickerComponent),\n      multi: true\n    },\n    {\n      provide: NgbDateParserFormatter,\n      useClass: SwissDateParserFormatter\n    },\n    {\n      provide: NgbDatepickerI18n,\n      useClass: DatepickerI18n\n    }\n  ]\n})\nexport class DatepickerComponent implements ControlValueAccessor {\n\n  @HostBinding('attr.id')\n  externalId = '';\n\n  @ViewChild(NgbInputDatepicker, {static: true})\n  datepicker: NgbInputDatepicker;\n\n  @ViewChild('dateButton')\n  button: HTMLButtonElement;\n\n  private _value;\n\n  public get value() {\n    return this._value;\n  }\n\n  public set value(v) {\n    this._value = v;\n    this.onChange(this._value);\n    this.onTouched();\n  }\n\n  private _id = '';\n\n  @Input()\n  set id(value: string) {\n    this._id = value;\n    this.externalId = null;\n  }\n\n  get id() {\n    return this._id;\n  }\n\n  constructor(private calendar: NgbCalendar) {\n  }\n\n  selectToday() {\n    this.value = this.calendar.getToday();\n    this.datepicker.navigateTo(this.value);\n  }\n\n  /**\n   * Empty listener, to be overwritten by 'registerOnChange'\n   */\n  onChange: (_: any) => void = (_: any) => {\n  };\n  /**\n   * Empty listener, to be overwritten by 'registerOnTouched'\n   */\n  onTouched: () => void = () => {\n  };\n\n  registerOnChange(fn: any): void {\n    this.onChange = fn;\n  }\n\n  registerOnTouched(fn: any): void {\n    this.onTouched = fn;\n  }\n\n  setDisabledState(isDisabled: boolean): void {\n    this.datepicker.disabled = isDisabled;\n    this.button.disabled = isDisabled;\n  }\n\n  writeValue(obj: any): void {\n    this.value = obj;\n    this.onChange(this.value);\n  }\n\n}\n";