import {Component, OnInit} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {LanguageService} from './language.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
})
export class AppComponent implements OnInit {
  toggleKey: string;

  maxContentWidth = '70em';

  constructor(private translateService: TranslateService, private languageService: LanguageService) {}

  onRouterOutletActivate(component: any) {
    if (component != null && typeof component.maxWidth === 'string') {
      this.maxContentWidth = component.maxWidth;
    } else {
      this.maxContentWidth = '70em';
    }
  }

  ngOnInit() {
    this.translateService.setDefaultLang(this.languageService.defaultLanguage());
    this.translateService.use(this.languageService.defaultLanguage());

    // we bundle the translations to prevent a flash of untranslated content
    // (this does not significantly increase bundle size unless you have dozens of translations)
    for (const lang of this.languageService.availableLanguages()) {
      this.translateService.setTranslation(lang, require(`../i18n/locale-${lang}.json`));
    }
  }

  toggleNavigation() {
    const e = document.body;
    if (e && e.classList) {
      const closed = e.classList.toggle('close-navigation');
      this.toggleKey = closed ? 'Open navigation' : 'Close navigation';
    }
  }
}
