export default "import {Component, EventEmitter, Input, Output, ViewEncapsulation} from '@angular/core';\nimport {Router} from '@angular/router';\n\n@Component({\n  selector: 'app-functionbar-item',\n  template: `\n    <li class=\"nav-item\">\n      <button type=\"button\" class=\"btn btn-light btn-functionbar btn-lg nav-item\"\n              [attr.title]=\"tooltipKey | translate\"\n              [attr.aria-label]=\"labelKey ? null : (tooltipKey | translate)\"\n              [disabled]=\"disabled\" (click)=\"onclick($event)\">\n        <i *ngIf=\"icon\" class=\"{{icon}} fa-fw\" aria-hidden=\"true\"></i>\n        <small *ngIf=\"labelKey\">{{labelKey | translate}}</small>\n      </button>\n    </li>\n  `,\n  encapsulation: ViewEncapsulation.None\n})\nexport class FunctionbarItemComponent {\n\n  @Input() routerLink: string;\n  @Input() icon: string;\n  @Input() tooltipKey: string;\n  @Input() labelKey: string;\n\n  @Input() disabled = false;\n\n  @Output() itemClicked = new EventEmitter<void>();\n\n  constructor(private router: Router) {\n  }\n\n  onclick(event: MouseEvent) {\n    this.itemClicked.emit();\n    if (this.routerLink) {\n      this.router.navigate([this.routerLink]);\n    }\n    event.stopPropagation();\n    // the (click) subscribers have also subscribed to the DOM event, and we don't want them to be invoked twice ...\n  }\n}\n";