export default "import {Component} from '@angular/core';\nimport {AlertHolderDirective} from '../../../bedag-components/alert/alert-holder.directive';\n\n@Component({\n  selector: 'app-alert-demo',\n  template: `\n    <div class=\"row\">\n      <div class=\"col-8 d-flex\">\n        <button class=\"btn btn-outline-primary w-100 mr-2\" (click)=\"infoAlert()\">\n          <span class=\"fas fa-info-circle text-info mr-2\"></span>Alert anzeigen\n        </button>\n        <button class=\"btn btn-outline-primary w-100 mr-2\" (click)=\"successAlert()\">\n          <span class=\"fas fa-check-circle text-success mr-2\"></span>Alert anzeigen\n        </button>\n        <button class=\"btn btn-outline-primary w-100 mr-2\" (click)=\"warningAlert()\">\n          <span class=\"fas fa-exclamation-triangle text-warning mr-2\"></span>Alert anzeigen\n        </button>\n        <button class=\"btn btn-outline-primary w-100\" (click)=\"dangerAlert()\">\n          <span class=\"fas fa-exclamation-circle text-danger mr-2\"></span>Alert anzeigen\n        </button>\n      </div>\n    </div>\n  `,\n  styleUrls: ['alert-demo.component.scss']\n})\nexport class AlertDemoComponent {\n\n  constructor(private alerts: AlertHolderDirective) {\n  }\n\n  infoAlert() {\n    this.alerts.info('Der Server steht wegen Wartungsarbeiten von 20.30 bis 24.00 Uhr nicht zur Verfügung.');\n  }\n\n  successAlert() {\n    this.alerts.success('Das Ticket wurde erstellt.');\n  }\n\n  dangerAlert() {\n    this.alerts.danger('Das Ticket konnte nicht gespeichert werden.');\n  }\n\n  warningAlert() {\n    this.alerts.warning('Automatische Updates wurden deaktiviert.');\n  }\n}\n";