import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {ExtraOptions, RouterModule, Routes} from '@angular/router';

import {AppComponent} from './app.component';
import {AppNavComponent} from './app-nav.component';
import {TranslateModule} from '@ngx-translate/core';
import {LanguageService} from './language.service';
import {SourceCodeService} from './source-code.service';
import {WebpackSourceCodeService} from './styleguide/webpack-source-code.service';
import {AlertHolderDirective} from './bedag-components/alert/alert-holder.directive';
import {BedagComponentsModule} from './bedag-components/bedag-components.module';
import {KeyboardShortcutsModule} from 'ng-keyboard-shortcuts';

const routerOptions: ExtraOptions = {
  scrollPositionRestoration: 'enabled',
  anchorScrolling: 'enabled',
  scrollOffset: [0, 64],
};

const routes: Routes = [
  {path: '', redirectTo: 'styleguide', pathMatch: 'full'},
  {path: 'styleguide', loadChildren: () => import('./styleguide/styleguide.module').then(m => m.StyleguideModule)},
];

@NgModule({
  bootstrap: [AppComponent],
  imports: [
    TranslateModule.forRoot(),
    BrowserModule,
    RouterModule.forRoot(routes, routerOptions),
    KeyboardShortcutsModule.forRoot(),
    BedagComponentsModule
  ],
  providers: [
    LanguageService,
    AlertHolderDirective,
    {provide: SourceCodeService, useClass: WebpackSourceCodeService}
  ],
  declarations: [
    AppComponent,
    AppNavComponent,
  ],
})
export class AppModule {
}
