import { Injectable } from '@angular/core';
import {SourceCodeService} from '../source-code.service';
const context = require.context('!!raw-loader!../', true, /.component.(html|ts)$/);

@Injectable({
  providedIn: 'root'
})
export class WebpackSourceCodeService implements SourceCodeService {

  constructor() {
  }

  sourceCode(filename: string): string {
    const key = ('./styleguide/' + filename).replace('styleguide/../', '');
    return context(key).default;
  }
}
