export default "import {AfterViewInit, Component, TemplateRef, ViewChild, ViewEncapsulation} from '@angular/core';\nimport {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';\nimport {KeyboardShortcutsComponent, ShortcutInput} from 'ng-keyboard-shortcuts';\n\n@Component({\n  selector: 'app-layout',\n  templateUrl: './tastaturbedienbarkeit.component.html',\n  encapsulation: ViewEncapsulation.None\n})\n\nexport class TastaturbedienbarkeitComponent implements AfterViewInit {\n\n  shortcuts: ShortcutInput[] = [];\n  modal: NgbModalRef;\n\n  @ViewChild(KeyboardShortcutsComponent) private keyboard: KeyboardShortcutsComponent;\n  @ViewChild('content') templateRef: TemplateRef<any>;\n  private modalRef: NgbModalRef;\n\n  constructor(private modalService: NgbModal) { }\n\n  ngAfterViewInit(): void {\n    this.shortcuts.push(\n      {\n        key: '?',\n        command: () => this.showModal(),\n        preventDefault: true\n      },\n      {\n        key: 'h',\n        command: () => this.showModal(),\n        preventDefault: true\n      },\n      {\n        key: ['k s'],\n        command: () => this.showModal(),\n        preventDefault: true\n      }\n    );\n  }\n\n  showModal() {\n    if (this.modalService.hasOpenModals()){\n      this.modal.close();\n    }\n    this.modal = this.modalService.open(this.templateRef);\n  }\n\n}\n";