export default "import {Component} from '@angular/core';\nimport {ModalDismissReasons, NgbModal, NgbModalOptions, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';\n\n@Component({\n  templateUrl: './modal-dialog.component.html',\n})\nexport class ModalDialogComponent {\n  assignees = ['Hans Olo', 'Paul Panther'];\n\n  constructor(private modalService: NgbModal) { }\n  modal: NgbModalRef;\n  closeResult: string;\n\n  smallDialogOptions: NgbModalOptions = {\n    size: 'sm',\n  };\n\n  largeDialogOptions: NgbModalOptions = {\n    size: 'lg',\n    backdrop: 'static',\n    keyboard: false\n  };\n\n  private static getDismissReason(reason: any): string {\n    if (reason === ModalDismissReasons.ESC) {\n      return 'by pressing ESC';\n    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {\n      return 'by clicking on a backdrop';\n    } else {\n      return  `with: \"${reason}\"`;\n    }\n  }\n\n  open(content, size) {\n    const dialogOptions = (size === 'lg') ? this.largeDialogOptions : this.smallDialogOptions;\n    this.modal = this.modalService.open(content, dialogOptions );\n    this.modal.result.then((result) => {\n      this.closeResult = `Closed with: \"${result}\"`;\n    }, (reason) => {\n      this.closeResult = `Dismissed ${ModalDialogComponent.getDismissReason(reason)}`;\n    });\n  }\n\n  saveIssue() {\n    this.modal.close('Anfrage erstellen')\n  }\n\n  closeYear() {\n    this.modal.close('Jahr abschliessen')\n  }\n}\n";