import {Directive} from '@angular/core';

/**
 * Gathers the alerts of all nested components (unless they have their own AlertHolder). The root AlertHolder is also
 * available to services.
 */
@Directive({
  selector: '[appAlertHolder]'
})
export class AlertHolderDirective {

  alerts: Alert[] = [];

  public info(message: string) {
    this.add({type: 'info', message});
  }

  public success(message: string) {
    this.add({type: 'success', message});
  }

  public warning(message: string) {
    this.add({type: 'warning', message});
  }

  public danger(message: string) {
    this.add({type: 'danger', message});
  }

  private add(alert: Alert) {
    this.alerts.push(alert);
    if (alert.type === 'success' || alert.type === 'info') {
      setTimeout(() => this.dismiss(alert), 5000);
    }
  }

  public dismiss(alert: Alert) {
    setTimeout(() => this.alerts = this.alerts.filter(a => a !== alert), 150);
  }
}

interface Alert {
  type: string;
  message: string
}

