import {Component, HostBinding, Input} from '@angular/core';

/**
 * Wraps app-nav-items and applies the appropriate styling.
 */
@Component({
  selector: 'app-nav-menu',
  template: '<ng-content></ng-content>',
})
export class NavMenuComponent {
  @Input()
  showIcons = true;

  @HostBinding('id') id = 'navmenu';

  @HostBinding('class') get class() {
    return this.showIcons ? 'flex-column w-100' : 'flex-column w-100 no-icons'
  };
}
