export default "import {Component, EventEmitter, Input, Output} from '@angular/core';\n\n@Component({\n  selector: 'app-alert',\n  template: `\n    <div\n      class=\"alert fade alert-{{ type }} clearfix\"\n      [class.alert-dismissible]=\"dismissible\"\n      [class.show]=\"!fading\"\n      role=\"alert\"\n    >\n      <button\n        type=\"button\"\n        class=\"close\"\n        [attr.aria-label]=\"'Close' | translate\"\n        (click)=\"fading = true; dismiss.emit()\"\n        *ngIf=\"dismissible\"\n      >\n        <span aria-hidden=\"true\">&times;</span>\n      </button>\n      <span class=\"fas {{ icons[type] }} mr-2\"></span>\n      <ng-content></ng-content>\n    </div>\n  `\n})\nexport class AlertComponent {\n  @Input()\n  type: string;\n\n  @Output()\n  dismiss = new EventEmitter<void>();\n\n  get dismissible() {\n    return this.dismiss.observers.length;\n  }\n\n  fading = false;\n\n  icons = {\n    danger: 'fa-exclamation-circle',\n    warning: 'fa-exclamation-triangle',\n    success: 'fa-check-circle',\n    info: 'fa-info-circle'\n  };\n}\n";