export default "import {Component, OnInit, ViewEncapsulation} from '@angular/core';\nimport {ADDRESS_DATA, AddressData} from './address-data';\nimport * as _ from 'underscore';\nimport {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';\nimport {AlertHolderDirective} from '../../../bedag-components/alert/alert-holder.directive';\n\n@Component({\n  selector: 'app-tables',\n  templateUrl: './datatables.component.html',\n  encapsulation: ViewEncapsulation.None\n})\nexport class DatatablesComponent implements OnInit {\n  maxWidth = '100%';\n\n  addressData = [];\n  pagedItems: Array<AddressData> = [];\n  selected: number[] = [];\n  sortedBy: string;\n  sortedAscending: boolean;\n  searchTerm: string;\n  page = 1;\n  pageSize = 5;\n  pageSizes = [5,10,25,100];\n  modal: NgbModalRef;\n  selectAll: boolean;\n  selectedForDetail: AddressData;\n\n  constructor(private modalService: NgbModal, private alerts: AlertHolderDirective) {\n  }\n\n  ngOnInit() {\n    this.addressData = ADDRESS_DATA;\n    this.sortBy('name');\n  }\n\n  getColumnClassNames(column: string) {\n    let classNames = 'fas fa-sort'\n    if (this.sortedBy === column){\n      classNames += this.sortedAscending ? '-up' : '-down';\n      classNames += ' text-primary';\n    }\n    return classNames;\n  }\n\n  sortBy(field: string) {\n    if (this.sortedBy === field) {\n      this.sortedAscending = !this.sortedAscending;\n      this.addressData.reverse();\n    } else {\n      this.sortedBy = field;\n      this.sortedAscending = true;\n      this.addressData = _.sortBy(this.addressData, field);\n    }\n\n    this.setPage(1);\n    return false; // do not follow the href\n  }\n\n  search() {\n    this.addressData = ADDRESS_DATA.filter(item => JSON.stringify(item).toLowerCase().indexOf(this.searchTerm.toLowerCase()) !== -1);\n\n    if (this.sortedBy) {\n      this.addressData = _.sortBy(this.addressData, this.sortedBy);\n\n      if (!this.sortedAscending) {\n        this.addressData.reverse();\n      }\n    }\n\n    this.setPage(1);\n  }\n\n  setPage(page: number) {\n    this.page = page;\n    const startIndex = (this.page - 1) * this.pageSize;\n    const endIndex = Math.min(startIndex + this.pageSize, this.addressData.length);\n    this.pagedItems = this.addressData.slice(startIndex, endIndex);\n    this.selected = [];\n    this.selectAll = false;\n  }\n\n  showModal(content, index: number) {\n    this.selectedForDetail = this.pagedItems[index];\n    this.modal = this.modalService.open(content);\n  }\n\n  isSelected(i: number) {\n    return this.selectAll || this.selected.includes(i);\n  }\n\n  toggleSelection(i: number) {\n    if (this.selected.includes(i)){\n      this.selected = this.selected.filter(n => n !== i);\n    } else {\n      this.selected.push(i);\n    }\n  }\n\n  toggleSelectAll(){\n    this.selectAll = !this.selectAll;\n    if (!this.selectAll) {\n      this.selected = [];\n    }\n  }\n\n  showAlert(address: AddressData, alertText: string) {\n    this.alerts.success(alertText + ' wurde ausgewählt für ' + address.vorname + ' ' + address.name);\n  }\n\n  processSelected(action: string) {\n    if (!this.selectAll && this.selected.length < 1){\n      this.alerts.success('Keine Einträge ausgewählt')\n    } else {\n    let text = action + ' ';\n      if (this.selectAll){\n        text += this.pagedItems.map(a => a.vorname + ' ' + a.name).join(', ');\n      } else {\n        text += this.selected.map(i => this.pagedItems[i].vorname + ' ' + this.pagedItems[i].name).join(', ');\n      }\n      this.alerts.success(text);\n    }\n  }\n}\n";